* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.state {
  max-width: 640px;
  width: calc(100% - 20px);
  margin: 0 auto;
  text-align: center;
  border: 5px dotted rgb(45, 136, 255);
  padding: 50px;
}

.state h1 {
  text-align: center;
}

.state input {
  border: 3px solid skyblue;
  width: 200px;
  height: 30px;
  padding: 0 10px;
  position: relative;
  margin-bottom: 30px;
}

.state img {
  width: 500px;
  height: 500px;
  object-fit: cover;
}

.state button {
  position: absolute;
  right: 40%;
  width: 40px;
  height: 30px;
  background-color: skyblue;
  border: none;
  color: #fff;
  border-radius: 5px;
}
